	export default {
		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0
				},
                content: ''
			}
		},
        onLoad() {
            const info = uni.getSystemInfoSync();
            this.style.pageHeight = info.windowHeight;
            this.style.contentViewHeight = info.windowHeight; //像素
            this.content = this.$store.state.userInfo.userText;
        },
        onNavigationBarButtonTap() {
            let userinfo = this.$store.state.userInfo;
            let userid = userinfo.userId;
            let usertoken = userinfo.loginToken;
            uni.request({
                url: this.websiteUrl + '/api/user/updBlurb',
                method: 'POST',
                header: {
                    Authorization: usertoken
                },
                data: {
                    userId: userid,
                    userBlurb: this.content
                },
                success: res => {
                    if (res.data.status == 0) {
                        userinfo.userText = res.data.user.userBlurb;
                        this.$store.commit('login',userinfo);
                        uni.showToast({
                            title: '保存成功',
                            mask: true
                        });
                        setTimeout(function(){
                            uni.hideToast();
                            uni.navigateBack();
                        },400)
                    } else if (res.data.status == 1001) {
                        uni.showToast({
                            title: res.data.message,
                            icon: 'none',
                            mask: true
                        });
                        setTimeout(function () {
                            uni.hideToast();
                            uni.navigateTo({
                                url:"/pages/login/login",
                                animationType:"slide-in-right"
                            });
                        }, 300)
                    } else {
                        uni.showToast({
                            title: res.data.message,
                            icon: 'none',
                            mask: true
                        });
                        setTimeout(function(){
                            uni.hideToast();
                        },2000)
                    }
                },
                fail: () => {},
                complete: () => {}
            });
        }
	}
